import {PageProps} from "gatsby"
import {GatsbySeo} from "gatsby-plugin-next-seo"
import {Fragment, useContext, useState} from "react"
import ContactUsForm from "~/components/Forms/ContactUsForm"
import Blank from "~/components/Sections/Blank/Blank"
import HeroSection from "~/components/Sections/HeroSection/HeroSection"
import Search from "~/components/Sections/Search/Search"
import {ShelfSortCategories} from "~/components/Sections/Shelf/Shelf"
import Button from "~/components/UI/Button/Button"
import Gap from "~/components/UI/Gap/Gap"
import GenericLink from "~/components/UI/GenericLink/GenericLink"
import InputText from "~/components/UI/InputText/InputText"
import Reader from "~/components/UI/Reader/Reader"
import RichTextField from "~/components/UI/RichText/RichText"
import ScrollButton from "~/components/UI/ScrollButton/ScrollButton"
import SectionContainer from "~/components/UI/SectionContainer/SectionContainer"
import Switch from "~/components/UI/Switch/Switch"
import AuthContext from "~/contexts/AuthContext"
import I18nContext from "~/contexts/I18nContext"
import TransitionContext from "~/contexts/TransitionContext"
import {useAnalysisPostsQuery} from "~/queries/AnalysisPostsQuery"
import {useAuthenticationLabelsQuery} from "~/queries/AuthenticationLabelsQuery"
import {useLabelsQuery} from "~/queries/LabelsQuery"
import {useLinksQuery} from "~/queries/LinksQuery"
import {useOnDemandPostsQuery} from "~/queries/OnDemandPostsQuery"
import {usePodcastPostsQuery} from "~/queries/PodcastPostsQuery"
import {useVideoPostsQuery} from "~/queries/VideoPostsQuery"
import {PostItem, PostTypes} from "~/types/PostItem"
import {generateQueryParams} from "~/utilities/queryParams"
import {
    PrivateAreaSearchParam,
    PrivateAreaSectionIndex,
    PrivateAreaSectionParam,
    privateAreaSectionsIndexToName,
} from "./PrivateAreaTemplate"

import {RichContent} from "../types/RichContent"
import "./MagazinePost.css"

interface PrivateAreaPostProps extends PageProps<any, PrivateAreaPostContext> {}

const PrivateAreaPost: React.FC<PrivateAreaPostProps> = props => {
    const privateAreaPostData = props.pageContext.data
    const privateAreaData = props.pageContext.secondaryData

    const i18n = useContext(I18nContext)
    const labels = {...useLabelsQuery(), ...useAuthenticationLabelsQuery()}
    const links = useLinksQuery()
    const privateAreaPosts: Array<PostItem> = []
    const authContext = useContext(AuthContext)
    const allPrivateAreaPosts = [
        ...useAnalysisPostsQuery(),
        ...useVideoPostsQuery(),
        ...usePodcastPostsQuery(),
        ...useOnDemandPostsQuery(),
    ]
    allPrivateAreaPosts.forEach(post => {
        if (post.type !== privateAreaPostData.type) return
        if (privateAreaPostData.slug === post.slug) return
        privateAreaPosts.push(post)
    })

    const [selectedSectionIndex, setSelectedSectionIndex] = useState<PrivateAreaSectionIndex | number>(0)
    const [inputTextValue, setInputTextValue] = useState("")
    const [selectedSectionName, setSelectedSectionName] = useState<ShelfSortCategories | string>("")
    const transitionContenxt = useContext(TransitionContext)

    const onInputChangeValue = (event: React.ChangeEvent<HTMLInputElement>) => {
        setInputTextValue(event.currentTarget.value)
    }
    const onSwitchClickHandler = (event: React.MouseEvent<HTMLButtonElement>) => {
        const selectedType = Number(event.currentTarget.value)
        setSelectedSectionIndex(selectedType)
        setSelectedSectionName(privateAreaSectionsIndexToName(selectedType))
    }
    const onFormSubmitHandler = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault()
        const queryParams = generateQueryParams(
            {name: PrivateAreaSectionParam, value: selectedSectionName},
            {name: PrivateAreaSearchParam, value: inputTextValue},
        )
        const url = `${links.privateArea.url}?${queryParams}`
        transitionContenxt.navigateTo(url)
    }

    let formattedDate
    if (privateAreaPostData.date) {
        formattedDate = new Intl.DateTimeFormat(i18n.language, {
            day: "numeric",
            month: "long",
            year: "numeric",
        }).format(new Date(privateAreaPostData.date))
    }

    let canonicalUrl = ""
    switch (privateAreaPostData.type) {
        case PostTypes.analysis:
            canonicalUrl = `${process.env.SITE_URL}${links.analysis.url}/${privateAreaPostData.slug}`
            break
        case PostTypes.video:
            canonicalUrl = `${process.env.SITE_URL}${links.video.url}/${privateAreaPostData.slug}`
            break
        case PostTypes.onDemand:
            canonicalUrl = `${process.env.SITE_URL}${links.onDemand.url}/${privateAreaPostData.slug}`
            break
        case PostTypes.podcast:
            canonicalUrl = `${process.env.SITE_URL}${links.podcast.url}/${privateAreaPostData.slug}`
            break
    }

    return (
        <Fragment>
            <GatsbySeo
                title={`${privateAreaPostData.title}`}
                canonical={canonicalUrl}
                description={`${privateAreaPostData.description_short}`}
                language={i18n.language}
                openGraph={{
                    url: canonicalUrl,
                    title: `${privateAreaPostData.title}`,
                    description: `${privateAreaPostData.description_short}`,
                    locale: i18n.language,
                    images: [
                        {
                            url: `${privateAreaPostData.image?.url}`,
                            width: Number(privateAreaPostData.image?.width),
                            height: Number(privateAreaPostData.image?.height),
                            alt: `${privateAreaPostData.image?.title}`,
                        },
                    ],
                }}
            />
            <HeroSection image={privateAreaPostData.image?.gatsbyImageData} imageAlt={privateAreaPostData.image?.title}>
                <div>
                    <h6 className="date-9jgh">{formattedDate}</h6>
                    <h3 className="title-9jgh">{privateAreaPostData.title}</h3>
                    <h6 className="category-9jgh">
                        {`${labels.category}: `}
                        <GenericLink
                            className="category-value-9jgh"
                            to={`${links.privateArea.url}?search=${privateAreaPostData.category}`}
                        >
                            {privateAreaPostData.category}
                        </GenericLink>
                    </h6>
                </div>
            </HeroSection>
            {authContext.status === "SIGNED_IN" && (
                <Fragment>
                    <SectionContainer>
                        <Search title={privateAreaData.titolo} startClosed>
                            <Gap centered style={{marginTop: "var(--gutter-4)"}}>
                                <Switch
                                    key={labels.all}
                                    selected={selectedSectionIndex === PrivateAreaSectionIndex.index}
                                    onSelect={onSwitchClickHandler}
                                    value={PrivateAreaSectionIndex.index}
                                >
                                    {labels.all}
                                </Switch>
                                {privateAreaData.filtri?.map((section, index) => {
                                    return (
                                        <Switch
                                            key={section?.slug}
                                            selected={selectedSectionIndex === index + 1}
                                            onSelect={onSwitchClickHandler}
                                            value={index + 1}
                                        >
                                            {section?.titolo}
                                        </Switch>
                                    )
                                })}
                            </Gap>
                            <form onSubmit={onFormSubmitHandler}>
                                <Gap centered>
                                    <InputText
                                        placeholder={labels.searchInputPlaceholder}
                                        onChange={onInputChangeValue}
                                        value={inputTextValue}
                                        style={{
                                            width: "min(30vw,500px)",
                                            minWidth: "240px",
                                            fontStyle: "italic",
                                        }}
                                    />
                                    <Button color="green" type="submit">
                                        {labels.search}
                                    </Button>
                                </Gap>
                            </form>
                        </Search>
                    </SectionContainer>
                    <SectionContainer>
                        <Blank>{privateAreaPostData.content && <Reader>{privateAreaPostData.content}</Reader>}</Blank>
                    </SectionContainer>
                    <SectionContainer className="contact-us-section-9jgh">
                        <ContactUsForm />
                    </SectionContainer>
                    {/* <SectionContainer>
                        <Shelf show={2} label={labels.more}>
                            {privateAreaPosts.map((item, index) => {
                                return (
                                    <Post
                                        key={index}
                                        date={item.date}
                                        title={item.title}
                                        image={item.image}
                                        category={item.category}
                                        tags={item.tags}
                                        description_short={item.description_short}
                                        slug={item.slug}
                                        type={item.type}
                                        customer={item.customer}
                                        project={item.project}
                                    />
                                )
                            })}
                        </Shelf>
                    </SectionContainer> */}
                </Fragment>
            )}
            {authContext.status !== "SIGNED_IN" && (
                <Fragment>
                    <SectionContainer tabbed marginPost customTab={<ScrollButton />}>
                        <Blank>
                            <RichTextField>{labels.accountRequired?.raw}</RichTextField>
                        </Blank>
                        <div className="switch-container-8hxd">
                            <span className="switch-label-8hxd">
                                {`${labels.missingAccount} `}
                                <GenericLink
                                    onClick={() => authContext.setStatus("SIGN_UP")}
                                    to={links.authentication.url}
                                >
                                    {labels.clickHereLinkLabel}
                                </GenericLink>
                            </span>
                        </div>
                    </SectionContainer>
                </Fragment>
            )}
        </Fragment>
    )
}

export default PrivateAreaPost

interface PrivateAreaPostContext {
    data: {
        customer?: string | null
        description_short?: string | null
        id: string
        type?: string | null
        title?: string | null
        slug?: string | null
        project?: string | null
        node_locale: string
        category?: string | null
        date?: any | null
        image?: {
            title?: string | null
            url?: string | null
            width?: number | null
            height?: number | null
            gatsbyImageData?: any | null
        } | null
        pdf?: {url?: string | null; title?: string | null} | null
        content?: RichContent | null
    }
    secondaryData: {
        id: string
        slug?: string | null
        titolo?: string | null
        node_locale: string
        filtri?: Array<{titolo?: string | null; slug?: string | null; node_locale: string} | null> | null
    }
}
